import { React, useEffect, useState, useContext } from "react";
import { Context } from "../../components/Reducer";
import { makeStyles } from "@material-ui/core/styles";
import { Paper, Typography, Button, Box, ButtonGroup } from "@material-ui/core";

import {
  Widget,
  addResponseMessage,
  renderCustomComponent,
} from "react-chat-widget";
import "react-chat-widget/lib/styles.css";
import "./chat.css";


//  Styles =======================================================
const useStyles = makeStyles((theme) => ({
  messageBubble: {
    padding: "1rem",
    backgroundColor: "#f4f7f9",
  },
  productButton: {
    width: "90%",
    paddingLeft: "5px",
    paddingRight: "5px",

    display: "flex",
    flexWrap: "wrap",
    justifyContent: "space-between",
    // marginTop: "5px",
    borderBottom: "1px solid #3F51B5",
  },
}));

//  Category View =======================================================
function CategoryPredictionView(props) {
  const classes = useStyles();

  return (
    <Paper className={classes.messageBubble} elevation={0}>
      <div>
        <Typography align="left">
          Will I be referring your query to the <br />
          <b>{props.predictionLabel}</b>
          <br />
          specialist?
        </Typography>
      </div>
      <Box my={2} display="flex">
        <Button
          variant="contained"
          color="primary"
          onClick={() => props.callback(props.predictionLabel)}
        >
          Refer to {props.predictionLabel}
        </Button>
      </Box>
      <Typography align="left">
        Or would you prefer to be referred to a different specialist?
      </Typography>
      <Box my={2} display="flex">
        <ButtonGroup
          orientation="vertical"
          color="primary"
          aria-label="other categories button group"
        >
          {props.alternateLabels.map((label, i) => (
            <Button key={i} onClick={() => props.callback(label)}>
              {label}
            </Button>
          ))}
          <Button onClick={() => props.callback("others")}>Others</Button>
        </ButtonGroup>
      </Box>
    </Paper>
  );
}

//  Product View =======================================================
function ProductPredictionView(props) {
  const classes = useStyles();

  return (
    <Paper className={classes.messageBubble} elevation={0}>
      <div>
        <Typography align="left">
          Is your query pertaining to any of these products?
        </Typography>
      </div>
      <Box my={2} display="flex" flexDirection="column">
        {props.products.map((productName, i) => (
          <div key={i} className={classes.productButton}>
            <Button color="primary" onClick={() => props.callback(productName)}>
              {productName}
            </Button>
            <Button href={props.links[i]} target="_blank" color="inherit">
              view
            </Button>
          </div>
        ))}
        <Button onClick={() => props.callback("others")}>
          None of the above
        </Button>
      </Box>
    </Paper>
  );
}

//  End View =======================================================
function EndView(props) {
  const classes = useStyles();

  return (
    <Paper className={classes.messageBubble} elevation={0}>
      <div>
        <Typography align="left">
          Thanks for trying the demo. If you wish to try again, click the button
          below.
        </Typography>
      </div>
      <Button
        variant="contained"
        color="primary"
        onClick={() => props.callback()}
      >
        Try Demo Again
      </Button>
    </Paper>
  );
}

// =================================================================
function ChatWidget() {
  // TODO: Change URI when I make it HTTPS
  const URI = "http://ec2-13-212-241-27.ap-southeast-1.compute.amazonaws.com:8080/api/";
  const [state, dispatch] = useContext(Context);

  const [query, setQuery] = useState("");

  const [conversationState, setconversationState] = useState("");
  // STATE MANAGEMENT =================================================================
  useEffect(() => {
    switch (conversationState) {
      case "start":
        addResponseMessage(
          "Welcome to the chat demo. Please ask a question about a product you have in mind."
        );
        // 2. listen for user to chat
        setconversationState("listenForQuery");
        break;
      case "requestCategoryFromServer":
        // 4. display categry selection and listen for selection
        requestCategory(query);
        break;
      case "listenForCategorySelection":
        displayCategories();
        break;
      case "requestProductFromServer":
        requestProduct();
        break;
      case "listenForProductSelection":
        displayProducts();
        break;
      case "connectToLiveOperator":
        addResponseMessage(
          "Please wait while I connect you to a product specialist."
        );
        renderCustomComponent(EndView, {
          callback: reset,
        });

        break;
    }
  }, [conversationState]);

  // CHAT API EVENT HANDLERS =================================================================
  function handleNewUserMessage(newMessage) {
    // message events
    switch (conversationState) {
      case "listenForQuery":
        // 3. user sent query
        setQuery(newMessage);
        dispatch({ type: "SET_QUERY", payload: newMessage });

        setconversationState("requestCategoryFromServer");
        break;
      default:
        break;
    }
  }
  // CUSTOM EVENT HANDLERS =================================================================

  function displayProducts() {
    renderCustomComponent(ProductPredictionView, {
      products: state.productsList,
      links: state.productsLinksList,
      callback: handleProductSelection,
    });
  }
  function handleProductSelection(selection) {
    dispatch({ type: "SET_PRODUCT", payload: selection });
    if (state.product == "others") {
      addResponseMessage(
        `That's alright. You can describe the product to our specialist later on.`
      );
    } else {
      addResponseMessage(
        `Thank you. You have chosen the product "${selection}".`
      );
    }

    setconversationState("connectToLiveOperator");
  }

  function requestProduct() {
    const msg = encodeURIComponent(query.trim());
    const category = encodeURIComponent(state.category.trim());
    fetch(`${URI}recommend-product/${msg}/${category}`)
      .then((res) => res.json())
      .then((res) => {
        console.log(">> requestProduct", res);
        dispatch({ type: "SET_PRODUCTS_LIST", payload: res.prediction });
        dispatch({ type: "SET_PRODUCTS_LINKS_LIST", payload: res.links });
        setconversationState("listenForProductSelection");
      });
  }

  useEffect(() => {
    // 1. initiate conversation
    setconversationState("start");
  }, []);

  const [predictionLabel, setPredictionLabel] = useState("");
  const [alternateLabels, setAlternateLabels] = useState([]);

  function requestCategory(query) {
    const msg = encodeURIComponent(query.trim());
    fetch(`${URI}predict-class/${msg}`)
      .then((res) => res.json())
      .then(
        (res) => {
          console.log(">> requestCategory", res);
          setPredictionLabel(res.predictionLabel);
          setAlternateLabels(res.alternateLabels);
          const energyList = strToNum(res.probabilities);
          dispatch({ type: "SET_CATEGORY_LIST", payload: res.categories });
          dispatch({ type: "SET_ENERGY_LIST", payload: energyList });
          setconversationState("listenForCategorySelection");
        },
        (err) => {
          console.log(err);
          addResponseMessage(`Error occured. Resetting simulation.`);
          reset();
        }
      );
  }
  function displayCategories() {
    renderCustomComponent(CategoryPredictionView, {
      predictionLabel: predictionLabel,
      alternateLabels: alternateLabels,
      callback: handleCategorySelection,
    });
  }
  function handleCategorySelection(selection) {
    dispatch({ type: "SET_CATEGORY", payload: selection });
    addResponseMessage(`Thank you. You have chosen "${selection}".`);
    if (selection == 'others') setconversationState("connectToLiveOperator");
    // TODO: Set back to normal flow when I deploy second model
    // else setconversationState("requestProductFromServer");
    else setconversationState("connectToLiveOperator");

  }

  function reset() {
    setAlternateLabels([]);
    setPredictionLabel("");
    setconversationState("start");
    setQuery("");
    dispatch({ type: "RESET" });
  }

  return (
    <Widget
      // profileAvatar={logo}
      title="BERT Customer Service Rep"
      subtitle="You're talking to a bot."
      handleNewUserMessage={handleNewUserMessage}
    />
  );
}

export default ChatWidget;

function strToNum(arr) {
  return arr.map(Number);
}
