import { React } from "react";

import { makeStyles } from "@material-ui/core/styles";
import { Paper, Typography } from "@material-ui/core";

import {
  BarChart,
  Bar,
  ReferenceLine,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  // Legend,
} from "recharts";

import data from "./data";

const sortedData = data.sort((a, b) => {
  if (a.count < b.count) return 1;
  if (a.count > b.count) return -1;
  return 0;
});

//  Styles =======================================================
const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexDirection: "column",
    padding: "1rem",
  },
  chartContainer: {
    margin: "auto",
  },
  header: {
    color: "gray",
  },
}));

// =================================================================
function DataDistributionSection() {
  const classes = useStyles();

  return (
    <Paper className={classes.root} align="center">
      <Typography variant="h6" align="right" className={classes.header}>
        DATA DISTRIBUTION OF 1,383,814 ENTRIES
      </Typography>

      <div className={classes.chartContainer}>
        <BarChart
          width={800}
          height={400}
          data={sortedData}
          margin={{ top: 10, right: 1, left: 1, bottom: 5 }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="name" type="category" />
          <YAxis type="number" />
          <Tooltip />
          <ReferenceLine y={0} stroke="#000" />
          <Bar dataKey="count" fill="#08519c" />
        </BarChart>
      </div>
    </Paper>
  );
}

export default DataDistributionSection;
