import { React } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Paper, Typography } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  root: {
    minHeight: "10rem",
    padding: theme.spacing(1),
  },
  centerParent: {
    minHeight: "9rem",
    display: "flex",
  },
  header: {
    color: "gray",
    display: "block",
    fontSize: "12px",
    fontWeight: "bold",
  },
  value: {
    display: "block",
    margin: "auto",
    fontSize: "4rem",
    fontWeight: "bold",
  },
}));

function ScoreTile(props) {
  const classes = useStyles();

  return (
    <Paper className={classes.root}>
      <Typography variant="h6" align="right" className={classes.header}>
        {props.header}
      </Typography>
      <div className={classes.centerParent}>
        <Typography variant="body1" align="right" className={classes.value}>
          {props.value}
        </Typography>
      </div>
    </Paper>
  );
}

export default ScoreTile;
