import { React, useContext } from "react";
import { Context } from "../../components/Reducer";
import { makeStyles } from "@material-ui/core/styles";
import { Paper, Typography } from "@material-ui/core";
import SectionHeader from "../../components/SectionHeader";

import ChatBubbleIcon from "@material-ui/icons/ChatBubble";

//  Styles =======================================================
const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexDirection: "column",
    padding: "1rem",
  },
  query: {
    fontSize: "2rem",
    // fontWeight: "600",
  },
  icon: {
    color: "#08519c",
    marginRight: "1rem",
    fontSize: "2rem",
    marginBottom: "-4px",
  },
}));

// =================================================================
function QueryDisplaySection() {
  const [state, dispatch] = useContext(Context);
  const classes = useStyles();

  return (
    <Paper className={classes.root} align="left">
      <SectionHeader content="QUERY" />
      {state.query.length > 0 && (
        <Typography className={classes.query}>
          <ChatBubbleIcon className={classes.icon} />
          {state.query}
        </Typography>
      )}
      {state.query.length == 0 && (
        <Typography align="center" color="primary">
          Please run simluation to view graph
        </Typography>
      )}
    </Paper>
  );
}

export default QueryDisplaySection;
