import React from "react";

import { makeStyles } from "@material-ui/core/styles";
import { Typography, Divider, Grid, Badge } from "@material-ui/core";
import ClearIcon from "@material-ui/icons/Clear";

const categoryList = [
  "Appliances",
  "Arts, Crafts and Sewing",
  "Automotive",
  "Baby",
  "Beauty",
  "Cell Phones and Accessories",
  "Clothing, Shoes and Jewelry",
  "Electronics",
  "Grocery and Gourmet Food",
  "Health and Personal Care",
  "Home and Kitchen",
  "Industrial and Scientific",
  "Musical Instruments",
  "Office Products",
  "Patio, Lawn and Garden",
  "Pet Supplies",
  "Software",
  "Sports and Outdoors",
  "Tools and Home Improvement",
  "Toys and Games",
  "Video Games",
];

//  Styles =======================================================
const useStyles = makeStyles((theme) => ({
  root: {
    padding: theme.spacing(1),
    height: "100%",
  },
  body: {
    marginTop: theme.spacing(1),
  },
  icon: {
    marginLeft: theme.spacing(1),
  },
}));
//  =======================================================
function WrongSampleTile(props) {
  const classes = useStyles();

  return (
    
      <div className={classes.root}>
        <Grid
          container
          direction="row"
          alignItems="flex-end"
          justify="flex-end"
        >
          <Grid item>
            <Typography className={classes.title} color="textSecondary">
              {categoryList[props.data.prediction]}
            </Typography>
          </Grid>
          <Grid item>
          <Badge badgeContent={props.data.true_pos} color="default">
            <ClearIcon
              fontSize="small"
              color="primary"
              className={classes.icon}
            />
            </Badge>
          </Grid>
        </Grid>
        <Divider />

        <Typography variant="body2" component="p" className={classes.body}>
          {props.data.text}
        </Typography>
      </div>
    
  );
}

export default WrongSampleTile;
