import React from "react";

import { useEffect, useState } from "react";

import { makeStyles } from "@material-ui/core/styles";
import { Paper, Typography, Grid } from "@material-ui/core";

import matrix from "../../assets/confusion_matrix.png";
import SectionHeader from "../../components/SectionHeader";
import Spacer from "../../components/Spacer";
import ScoreTile from "./ScoreTile";

//  Styles =======================================================
const useStyles = makeStyles((theme) => ({
  root: {
    padding: "1rem",
  },
  matrix: {
    display: "block",
    margin: "auto",
    width: "100%",
  },
}));
//  =======================================================
function ModelEvaluationSection() {
  const classes = useStyles();

  const [isLoaded, setIsLoaded] = useState(false);
  useEffect(() => {
    setIsLoaded(true);
  }, []);

  return (
    <React.Fragment>
      <Grid container spacing={3}>
        <Grid item xs={6} sm={3}>
          <ScoreTile header="WEIGHTED F1-SCORE" value="64%" />
        </Grid>
        <Grid item xs={6} sm={3}>
          <ScoreTile header="COHEN KAPPA" value=".6" />
        </Grid>
        <Grid item xs={6} sm={3}>
          <ScoreTile header="ACCURACY" value="65%" />
        </Grid>
        <Grid item xs={6} sm={3}>
          <ScoreTile header="MACRO RECALL" value="51%" />
        </Grid>
      </Grid>
      <Spacer />
      <Paper className={classes.root}>
        <SectionHeader content="CONFUSION MATRIX" />

        <img src={matrix} className={classes.matrix} alt="logo" />
      </Paper>
    </React.Fragment>
  );
}

export default ModelEvaluationSection;
