import React from "react";

import { makeStyles } from "@material-ui/core/styles";
import { Divider, Paper, Typography } from "@material-ui/core";
import TextLoop from "react-text-loop";
//  Styles =======================================================
const useStyles = makeStyles((theme) => ({
  root: {
    padding: theme.spacing(1),
    minHeight: "10rem",
  },
  blockText: {
    display: "block",
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
  },
  categoryText: {
    fontSize: "12px",
    fontWeight: "bold",
    display: "block",
    textAlign: "right",
    textTransform: 'uppercase',
    color: 'gray',
  },
  wordText: {
    fontSize: "16px",
    fontWeight: "900",
  },
}));
//  =======================================================
function CategoryWordTile(props) {
  const classes = useStyles();

  return (
    <Paper className={classes.root}>
      <Typography variant="subtitle1" className={classes.categoryText}>
        {props.data.category}
      </Typography>

      <TextLoop mask={true} fade={false} className={classes.blockText}>
        {props.data.topWords.slice(0, 14).map((d, i) => (
          <Typography key={i} className={classes.wordText}>
            {d}
          </Typography>
        ))}
      </TextLoop>
      <Divider />
      <TextLoop
        delay={1000}
        mask={true}
        fade={false}
        className={classes.blockText}
      >
        {props.data.topWords.slice(14, 28).map((d, i) => (
          <Typography key={i} className={classes.wordText}>
            {d}
          </Typography>
        ))}
      </TextLoop>
      <Divider />
      <TextLoop
        delay={2000}
        mask={true}
        fade={false}
        className={classes.blockText}
      >
        {props.data.topWords.slice(28, 41).map((d, i) => (
          <Typography key={i} className={classes.wordText}>
            {d}
          </Typography>
        ))}
      </TextLoop>
    </Paper>
  );
}

export default CategoryWordTile;
