const Reducer = (state, action) => {
  switch (action.type) {
    case "RESET":
      return {
        ...state,
        query: "",
        category: "",
        product: "",
        categoryList: [],
        energyList: [],
        productsList: [],
        productsLinksList: [],
      };
    case "SET_QUERY":
      return {
        ...state,
        query: action.payload,
      };
    case "SET_CATEGORY":
      return {
        ...state,
        category: action.payload,
      };
    case "SET_PRODUCT":
      return {
        ...state,
        product: action.payload,
      };
    case "SET_CATEGORY_LIST":
      return {
        ...state,
        categoryList: action.payload,
      };

    case "SET_ENERGY_LIST":
      return {
        ...state,
        energyList: action.payload,
      };
    case "SET_PRODUCTS_LIST":
      return {
        ...state,
        productsList: action.payload,
      };
    case "SET_PRODUCTS_LINKS_LIST":
      return {
        ...state,
        productsLinksList: action.payload,
      };
    default:
      return state;
  }
};

export default Reducer;
