import { React, useEffect, useState, useContext } from "react";
import { Context } from "../../components/Reducer";
import { makeStyles } from "@material-ui/core/styles";
import { Paper, Typography } from "@material-ui/core";
import SectionHeader from "../../components/SectionHeader";
import {
  BarChart,
  Bar,
  ReferenceLine,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  // Legend,
} from "recharts";

//  Styles =======================================================
const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexDirection: "column",
    padding: "1rem",
  },
  chartContainer: {
    margin: "auto",
  },
}));

// =================================================================
function EnergyGraphWidget() {
  const [state, dispatch] = useContext(Context);
  const classes = useStyles();

  const [data, setData] = useState([]);
  useEffect(() => {
    var d = [];
    for (var i = 0; i < state.categoryList.length; i++) {
      d.push({ name: state.categoryList[i], energy: state.energyList[i] });
    }
    setData(d);
  }, [state]);

  return (
    <Paper className={classes.root} align="center">
      <SectionHeader content="PREDICTION LIKELIHOOD" />
      {state.energyList.length > 0 && (
        <div className={classes.chartContainer}>
          <BarChart
            width={800}
            height={600}
            data={data}
            margin={{ top: 5, right: 1, left: 1, bottom: 5 }}
            layout="vertical"
          >
            <CartesianGrid strokeDasharray="3 3" />
            <YAxis dataKey="name" type="category" interval={0} width={200} />
            <XAxis type="number" />
            <Tooltip />
            <ReferenceLine y={0} stroke="#000" />
            <Bar dataKey="energy" fill="#08519c" />
          </BarChart>
        </div>
      )}
      {state.energyList.length == 0 && (
        <Typography align="center" color="primary">Please run simluation to view graph</Typography>
      )}
    </Paper>
  );
}

export default EnergyGraphWidget;
