import { React, useState } from "react";

import { makeStyles } from "@material-ui/core/styles";
import {
  Grid,
  Typography,
  Divider,
  Box,
  Accordion,
  AccordionSummary,
  AccordionDetails,
} from "@material-ui/core";
import CorrectSampleTile from "./CorrectSampleTile";
import WrongSampleTile from "./WrongSampleTile";

import correctSamples from "./correct_samples";
import wrongSamples from "./wrong_samples";
import Spacer from "../../components/Spacer";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
var corrects = [];
var wrongs = [];

for (var i = 0; i < 21; i++) {
  const groupC = correctSamples.slice(i * 10, i * 10 + 9);
  const groupW = wrongSamples.slice(i * 10, i * 10 + 9);

  corrects.push(groupC);
  wrongs.push(groupW);
}

const categoryList = [
  "Appliances",
  "Arts, Crafts and Sewing",
  "Automotive",
  "Baby",
  "Beauty",
  "Cell Phones and Accessories",
  "Clothing, Shoes and Jewelry",
  "Electronics",
  "Grocery and Gourmet Food",
  "Health and Personal Care",
  "Home and Kitchen",
  "Industrial and Scientific",
  "Musical Instruments",
  "Office Products",
  "Patio, Lawn and Garden",
  "Pet Supplies",
  "Software",
  "Sports and Outdoors",
  "Tools and Home Improvement",
  "Toys and Games",
  "Video Games",
];

//  Styles =======================================================
const useStyles = makeStyles((theme) => ({
  root: {
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3),
  },
  header: {
    color: "gray",
  },
  divider: {
    marginTop: theme.spacing(5),
    marginBottom: theme.spacing(5),
  },
}));

//  =======================================================
function SampleSection() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Box mb={2}>
        <Typography variant="h6" align="right" className={classes.header}>
          WRONGLY CLASSIFIED SAMPLES
        </Typography>
        <Divider />
      </Box>
      {wrongs.map((group, i) => (
        <Box mt={1} key={i}>
          <Accordion>
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls={`w-${i}`}
              id={`w-${i}`}
            >
              <Typography className={classes.heading}>
                {categoryList[i]}
              </Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Grid container spacing={3} alignItems="stretch" direction="row">
                {group.map((d, j) => (
                  <Grid item xs={12} sm={4} key={j}>
                    <WrongSampleTile data={d} index={j} />
                  </Grid>
                ))}
              </Grid>
            </AccordionDetails>
          </Accordion>
        </Box>
      ))}

      <Spacer />
      <Box mb={2}>
        <Typography variant="h6" align="right" className={classes.header}>
          CORRECTLY CLASSIFIED SAMPLES
        </Typography>
        <Divider />
      </Box>
      {corrects.map((group, i) => (
        <Box mt={1} key={i}>
          <Accordion>
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls={`c-${i}`}
              id={`c-${i}`}
            >
              <Typography className={classes.heading}>
                {categoryList[i]}
              </Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Grid container spacing={3} alignItems="stretch" direction="row">
                {group.map((d, j) => (
                  <Grid item xs={12} sm={4} key={j}>
                    <CorrectSampleTile data={d} index={j} />
                  </Grid>
                ))}
              </Grid>
            </AccordionDetails>
          </Accordion>
        </Box>
      ))}
    </div>
  );
}

export default SampleSection;
